@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

:root {
  --black: #333;
  --green: #017143;
  --white: #fff;
  --white2: #fff;
  --black2: #333;
}

.dark {
  --black: #fff;
  --white: #113828;
  --black2: #124e36;
}

body {
  overflow-x: hidden;
}

section {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  background: #fff;
  background: var(--white);
}

@media (max-width: 991px) {
  section {
    padding: 150px 20px;
  }
}

@media only screen and (max-width: 320px) {
  body { 
     font-size: 1.4em;
  }
}
 header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    z-index: 10000;
  }
  
  header .logo {
    position: relative;
    max-width: 70px;
  }
  
  header ul {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  header ul li {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    margin-left: 30px;
  }
  
  header ul li * {
    display: inline-block;
    color: var(--black);
    font-weight: 500;
    text-decoration: none;
  }

  .themeSwitch {
    position: relative;
    width: 40px;
    height: 40px;
    background: var(--black);
    border-radius: 50%;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }

  .themeSwitch::before {
    content: '\f186';
    font-family: fontAwesome;
    color: var(--white);
    font-size: 20px;
  }

  .themeSwitch.active::before {
    content: '\f185';
  }

  @media (max-width: 991px) {
    header {
      padding: 20px;
    }

    .navigation {
      display: none;
    }

    .navigation.active {
      display: -webkit-flex;
      display: flex;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      background: var(--white);
      -webkit-flex-direction: column;
              flex-direction: column;
    }

    header ul li {
      margin: 10px 0;
      margin-left: 0;
    }

    header ul li a {
      font-size: 1.5em;
      font-weight: 300;
    }

    .toggle {
      position: relative;
      width: 30px;
      height: 30px;
      background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAC8AAAAfCAYAAAB3XZQBAAAABGdBTUEAALGPC/xhBQAAACBjSFJNAAB6JgAAgIQAAPoAAACA6AAAdTAAAOpgAAA6mAAAF3CculE8AAAABmJLR0QAAAAAAAD5Q7t/AAAACXBIWXMAAAsSAAALEgHS3X78AAAAM0lEQVRYw+3WsREAIAzEMML+Oz8FQxAu0gQuXUmyPrVfB4gHALjKVYofFg8AbRkz8dPiD+qQDAquHlwPAAAAAElFTkSuQmCC);
      -webkit-filter: invert(1);
              filter: invert(1);
      background-size: 30px;
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;
      z-index: 1000;
    }

    .toggle.active {
      background: url(data:image/png;base64,);
      -webkit-filter: invert(1);
              filter: invert(1);
      background-size: 25px;
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;
    }

    .dark .toggle {
      -webkit-filter: invert(0);
              filter: invert(0);
    }
  }
.content {
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    z-index: 2;
}

.content .textBox {
    position: relative;
    max-width: 600px;
    z-index: 1;
    margin-right: 20px;
}

.content .textBox h2 {
    font-size: 3em;
    color: var(--black);
    line-height: 1.4em;
    font-weight: 700;
    margin-bottom: 10px;
}

.content .textBox h2 span {
    font-size: 1.5em;
    color: var(--green);
}

.content .textBox a {
    display: inline-block;
    margin-top: 20px;
    padding: 8px 20px;
    background: var(--green);
    color: var(--white2);
    border-radius: 40px;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: none;
}

.content .imgBox {
    position: relative;
    width: 500px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.content .imgBox::before {
    content: '';
    position: absolute;
    width: 350px;
    height: 300px;
    background: var(--green);
    border-radius: 50%;
}

.dark .content .textBox h2 span {
    font-size: 1.5em;
    color: var(--green);
}

.content .textBox p {
    color: var(--black);
}

.content .imgBox img {
    position: relative;
    z-index: 1;
    max-width: 350px;
    height: 400px;
}

.sci {
    position: absolute;
    bottom: 40px;
    left: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.sci li {
    list-style: none;
}

.sci li a {
    display: inline-block;
    margin-right: 15px;
    width: 50px;
    height: 50px;
    background: var(--black2);
    color: var(--white2);
    border-radius: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
}

.sci li a .fa {
    font-size: 1.2em;
}

.sci li a:hover {
    background: var(--green);
}

@media (max-width: 991px) {
    .sci {
        bottom: 20px;
        left: 20px;
    }

    .content {
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
    }

    .content .textBox,
    .content .imgBox {
        max-width: 60%;
        margin-right: 0px;
    }

    .content .imgBox img {
        max-width: 250px;
    }

    .content .imgBox::before {
        -webkit-clip-path: circle(125px at center);
                clip-path: circle(125px at center);
    }

    .content .textBox h2 {
        font-size: 1.75em;
    }
  }
